@import url(https://fonts.googleapis.com/css?family=Walter+Turncoat&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

html {
  font-family: "Walter Turncoat", cursive; }

body {
  padding-left: 40px; }

.main-title {
  margin: 30px 0;
  color: #5f6aff; }

.game {
  display: flex;
  align-items: center;
  flex-direction: column; }
  .game__boxes {
    width: 300px;
    display: flex;
    flex-wrap: wrap; }
  .game__box {
    font-family: "Walter Turncoat", cursive;
    width: 100px;
    height: 100px;
    border: 1px solid black;
    font-size: 48px;
    font-weight: bolder;
    text-align: center;
    line-height: 100px;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    cursor: pointer;
    outline: none; }
    .game__box--1, .game__box--2, .game__box--3 {
      border-top: none; }
    .game__box--3, .game__box--6, .game__box--9 {
      border-right: none; }
    .game__box--7, .game__box--8, .game__box--9 {
      border-bottom: none; }
    .game__box--1, .game__box--4, .game__box--7 {
      border-left: none; }
  .game__message {
    height: 50px;
    line-height: 50px; }
  .game__new-game-btn {
    outline: none;
    cursor: pointer;
    font-size: 24px;
    font-family: "Walter Turncoat", cursive;
    border-color: #5f6aff;
    padding: 0 10px;
    -webkit-transition: color 1s, background-color 1s;
    transition: color 1s, background-color 1s; }
    .game__new-game-btn:hover {
      background-color: #5f6aff;
      color: white; }

